import React, { useContext } from 'react';
import styled, { css } from 'styled-components';
import { FormingIdea } from './images';
import { DataContext } from '../context/DataContext';
import { English, Spanish } from './languageinfo';

const InterestContainer = styled.div`
    width: 100%;

    @media (max-width: 608px) {
        display: none;
    }

    @media (min-width: 1024px) {
        margin-top: 220px;
    }
`
const InterestSection = styled.div`
    width: 80%;
    margin: 0 150px;
    display: flex;
    flex-direction: row;

    @media (max-width: 1024px) {
        margin: 0 10%;
    }
`

const InterestInfo = styled.div`
    display: flex;
    flex-direction: column;
    padding: 20px;
    justify-content: center;
    margin-left: 5%;
`

const InterestImage = styled.div`
  width: 734px;
  height: 532px;

  @media (max-width: 1024px) {
    width: 334px;
  }
`

const Interest = () => {
    const { language } = useContext(DataContext);

    return ( 
        <InterestContainer>
            <InterestSection>
                <InterestImage>
                    <FormingIdea css={css`
                        width: 640px;

                        @media (max-width: 1024px) {
                            width: 334px;
                        }
                        @media (max-width: 768px) {
                            width: 204px;
                        }
                    `} />
                </InterestImage>
                <InterestInfo>
                    <h2>{language === 'english' ? English.titleInterest : Spanish.titleInterest}</h2>
                    <p className="subinfo" css={css`
                        margin-top: 80px;
                    `}>{language === 'english' ? English.textInterest : Spanish.textInterest}</p>
                </InterestInfo>
            </InterestSection>
        </InterestContainer>
     );
}
 
export default Interest;