import React, { useContext } from 'react';
import Hero from './hero';
import Interest from './interest';
import Experience from './experience';
import Knowledge from './knowledge';
import Touch from "../components/touch"
import Footer from './footer';
import styled, { css } from 'styled-components';
import { Language } from './images';
import { DataContext } from "../context/DataContext";

const ButtonFixed = styled.button`
  width: 54px;
  height: 54px;
  border: none;
  border-radius: 50%;
  bottom: 144px;
  right: 44px;
  z-index: 10;
  position: fixed;

  &:focus {
    outline: 0;
  }
`

const MainContent = () => {
    const { language, toogleLanguage } = useContext(DataContext);
  
    return ( 
        <>
        <ButtonFixed onClick={toogleLanguage} className={language === 'english' ? 'buttonLanguageActive' : 'buttonLanguage'}>
              <Language css={css`
                opacity: ${ language === 'spanish' ? '100%' : '50%' };
              `} className="styleLanguage"/>
        </ButtonFixed>
        <Hero />
        <Interest />
        <Experience />
        <Knowledge />
        <Touch />
        <Footer />
        </>
     );
}
 
export default MainContent;